import util, { parGetData, parSetData, checklogin } from '../../../common/util';
export default {
	data() {
		return {
			account: '',
			password: '',
			openid: '',
			type: 'login',
			msg: '如果你有【每日投稿】app帐号，可在此绑定，如果没有，则无需绑定'
		};
	},
	methods: {
		bindLogin() {
			if (this.account.length < 5) {
				uni.showToast({
					icon: 'none',
					title: '账号最短为 5 个字符'
				});
				return;
			}
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			uni.showLoading({
				title: '登录中',
				icon: 'none'
			});
			const data = {
				username: this.account,
				password: this.password,
				openid: this.openid,
				shebei: util.shebei
			};
			var self = this;
			// 去登录
			uni.request({
				url: util.apiurl + 'syj/index/loginbyname',
				method: 'POST',
				data: data,
				success: ret => {
					if (ret.statusCode == 200 && ret.data.code < 1) {
						// 绑定成功
						self.msg = '绑定成功';
						if (ret.data.username) {
							// 已绑定app帐号
							// 存入appusername
							uni.setStorageSync('appusername', parSetData(ret.data.username));
						}
						uni.switchTab({
							url: '/pages/center/center'
						});
					} else {
						self.msg = ret.data.msg;
						setTimeout(function() {
							uni.hideLoading();
						}, 1000);
					}
				},
				complete: function() {
					uni.hideLoading();
				},
				header: {
					auth: util.httpauth
				}
			});
		}
	},
	onShow() {
		var logininfo = util.checklogin();
		this.openid = logininfo.openid;
	}
};
